import axios from 'axios';
import store from 'store';
import { addAsyncCountValue, minusAsyncCountValue } from 'store/spinner-store';
import toastHook from 'hooks/useToastHook';
import popupHook from 'hooks/usePopupHook';

const BASE_URL = process.env.REACT_APP_API;
const { dispatch } = store;

const Axios = axios.create({
  baseURL: BASE_URL,
  timeout: 5000,
  withCredentials: true,
});

// axios     
Axios.interceptors.request.use(
  (config) => {
    dispatch(addAsyncCountValue());
    return config;
  },
  (error) => Promise.reject(error),
);

// api   response   return
Axios.interceptors.response.use(
  (response) => {
    //   
    dispatch(minusAsyncCountValue());
    return response;
  },
  (error) => {
    const [showToast] = toastHook();
    const [showPopup] = popupHook();
    //  403, 400, 500   
    //   
    dispatch(minusAsyncCountValue());
    const errorStatus = error.response.status;

    //         
    if (errorStatus === 401) {
      showPopup({
        title: `     \n     .`,
        btnMsg: '  ',
        goLink: '/login',
      });
    }

    //         
    if (errorStatus === 403) {
      showToast({ type: 'error', content: '   .' });
    }

    //   
    if (errorStatus === 409) {
      showToast({ type: 'error', content: '   .' });
    }
    //   
    // if (errorStatus === 401) {
    //   showToast({ type: 'error', content: ' .' });
    //   window.location = '/';
    // }

    return Promise.reject(error);
  },
);

export default Axios;
