import React, { useState, useEffect } from 'react';
import { checkSpecial, checkLetter } from 'utils';

const FindIdPassword = () => {
  const [paswword, setPassword] = useState('');
  const [paswwordCheck, setPasswordCheck] = useState('');
  const findPassword = () => {};

  const checkPass = () => {
    return checkLetter(paswword) && checkSpecial(paswword) && paswword.length > 7 && paswword.length < 21 && paswword === paswwordCheck;
  };

  return (
    <div className="sub layout-sub">
      <div className="col-12 col-center mw-430 pl15 pr15">
        <div className="pr15 pl15 find-container">
          <div className="mb48 tc">
            <h3> </h3>
          </div>

          <form onSubmit={findPassword}>
            <div className="mb10">
              <h5 className="mb8"> </h5>
              <input value={paswword} type="password" placeholder="  ." onChange={(e) => setPassword(e.target.value)} className="input-style1" autoComplete="on" />
            </div>

            <div className="d-flex x-eq mb16">
              <div className={`check-pass ${checkLetter(paswword) ? 'active' : ''}`}>
                <p className="caption-font"> /</p>
              </div>

              <div className={`check-pass ${checkSpecial(paswword) ? 'active' : ''}`}>
                <p className="caption-font">/ </p>
              </div>

              <div className={`check-pass ${paswword.length > 7 && paswword.length < 21 ? 'active' : ''}`}>
                <p className="caption-font">8 ~ 20</p>
              </div>
            </div>

            <div className="mb20">
              <h5 className="mb8">  </h5>
              <input
                value={paswwordCheck}
                type="password"
                placeholder="   ."
                onChange={(e) => setPasswordCheck(e.target.value)}
                className="input-style1"
                autoComplete="on"
              />
            </div>

            <div className="">
              <input type="submit" className="btn-style1 wid100 btn-font font-white middle" disabled={!checkPass()} value="  " />
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default FindIdPassword;
